  methods: {
    // 下载
    download(file){
      window.open(`${file}`)
    },
    // 初始化
    init(id,type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if(this.type=='info'||this.type=='else'){
        this.info(id);
      }else if(this.type=='logistics'){
        this.logistics=false;
        this.info(id);
      }else if(this.type=='cross'){
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj){
          if(o=='gongzimingcheng'){
            this.ruleForm.gongzimingcheng = obj[o];
	    this.ro.gongzimingcheng = true;
            continue;
          }
          if(o=='gongziyuefen'){
            this.ruleForm.gongziyuefen = obj[o];
	    this.ro.gongziyuefen = true;
            continue;
          }
          if(o=='yuangonggonghao'){
            this.ruleForm.yuangonggonghao = obj[o];
	    this.ro.yuangonggonghao = true;
            continue;
          }
          if(o=='yuangongxingming'){
            this.ruleForm.yuangongxingming = obj[o];
	    this.ro.yuangongxingming = true;
            continue;
          }
          if(o=='xingbie'){
            this.ruleForm.xingbie = obj[o];
	    this.ro.xingbie = true;
            continue;
          }
          if(o=='bumen'){
            this.ruleForm.bumen = obj[o];
	    this.ro.bumen = true;
            continue;
          }
          if(o=='zhiwei'){
            this.ruleForm.zhiwei = obj[o];
	    this.ro.zhiwei = true;
            continue;
          }
          if(o=='dixin'){
            this.ruleForm.dixin = obj[o];
	    this.ro.dixin = true;
            continue;
          }
          if(o=='jixiao'){
            this.ruleForm.jixiao = obj[o];
	    this.ro.jixiao = true;
            continue;
          }
          if(o=='jiangjin'){
            this.ruleForm.jiangjin = obj[o];
	    this.ro.jiangjin = true;
